@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
.taskCounter {
    color: darkgray;
    font-size: 14px;
    margin-top: 0;
  }
  
.header {
    color: snow;
    position: relative;
    max-width: 402px;
  }
  
  .title {
    margin-bottom: 12px;
  }
  
  .taskInfo {
    align-items: center;
    display: flex;
  }
  
  .remaining {
    margin-right: 12px;
  }
  
.taskAdd {
    margin-bottom: 54px;
    max-width: 402px;
    position: relative;
  }
  
  .input {
    background: black;
    border: 1px solid var(--color-space-gray);
    border-right: none;
    border-radius: 4px 0 0 4px;
    color: snow;
    height: 42px;
    font-size: 16px;
    padding: 12px;
    width: calc(100% - 60px);
  }
  
  .input:focus {
    outline: none;
    border: 1px solid deepskyblue;
  }
  
  .input::-webkit-input-placeholder {
    color: var(--color-space-gray);
    font-size: 14px;
  }
  
  .input:-ms-input-placeholder {
    color: var(--color-space-gray);
    font-size: 14px;
  }
  
  .input::placeholder {
    color: var(--color-space-gray);
    font-size: 14px;
  }
  
  .button {
    background: var(--color-dark-charcoal);
    border: none;
    border-radius: 0 4px 4px 0;
    color: white;
    cursor: pointer;
    font: 500 14px 'Montserrat', sans-serif;
    height: 42px;
    line-height: 42px;
    position: absolute;
    right: 0;
    text-transform: uppercase;
    top: 0;
    width: 60px;
  }
  
.list {
    list-style: none;
    margin: 0;
    max-width: 800px;
    padding: 0;
  }
  
  .task {
    align-items: center;
    background: black;
    border-radius: 4px;
    color: snow;
    display: flex;
    height: 60px;
    justify-content: space-between;
    margin-bottom: 6px;
    padding: 0 24px;
    position: relative;
  }
  
  li.done {
    background: var(--color-dark-charcoal);
    color: snow;
  }
  
  .done .taskName {
    text-decoration: line-through;
  }
  
  .taskInfo {
    align-items: center;
    display: flex;
  }
  
  .taskName {
    display: inline-block;
    margin: 0;
  }
  
  .emptyList {
    background: black;
    border-radius: 4px;
    color: snow;
    padding: 12px;
  }
  
  .emptyListIcon {
    margin-right: 6px;
  }
  
  .completeButton {
    color: azure;
    font-size: 24px;
    margin-right: 12px;
  }
  
  .deleteButton {
    color: snow;
    font-size: 18px;
  }
  
.tasks {
    margin: 0 auto;
    padding: 36px 0;
    width: 80%;
  }
  
/* Google font Montserrat */


/* Variables  */
:root {
  --color-dark-charcoal: #333;
  --color-midnight-black: #262626;
  --color-space-gray: #4c4c4c;
}

/* Reset and globals */

* { box-sizing: border-box; }

body {
  background: #262626;
  background: var(--color-midnight-black);
  font-family: 'Montserrat', sans-serif;
  margin: 0;
}

button {
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
}

